@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  background: #01F50D;
  color: #000000;
  font-family: 'Readex Pro', sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #010102;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.TodoCounter{
    text-align: center;
    font-size: 90px;
    color: white;
    text-shadow: 8px 4px 1px #0DFA9F;
    margin-bottom: 5px;
    margin-top: 70px;
}   

/*
rgba(250, 238, 226, 0.5)*/
.TodoSearch{
  border-radius: 8px;
  border: 2px solid #48DE0B;
  background-color: #A4FA0D;
  text-align: center;
  width: 30%;
  margin: 20px auto; 
  height: 40px;
  font-weight: bold;
  box-shadow: 0px 3px 15px #48DE0B; 
  color: black;
}
section{
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: 25px auto; 
    height: auto;
    background-color: #0BDE51;
    border-radius:20px ;
    border: 2px solid #48DE0B ;
    box-shadow: 0px 3px 15px #48DE0B; 
}
ul{
    padding: 0;
}
.CreateTodoButton {
    background-color: #0DFA9F;
    box-shadow: 0px 5px 25px #0BDE51;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 70px;
    position: fixed;
    bottom: 70px;
    right: 510px;
    font-weight: bold;
    color: #f6faf7;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 94px;
    width: 94px;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
  }
  
  .CreateTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }

  

  @media (max-width: 940px) {
    .CreateTodoButton{
      bottom: 40px;
      right: 240px;
      height: 80px;
      width: 80px;
      position: fixed;
    }
  }

  
  @media (max-width: 640px) {
    .CreateTodoButton{
      bottom: 0px;
      right: 300px;
      height: 60px;
      width: 60px;
      position: fixed;
    }
  }
  .CreateTodoButton{
    z-index: 1;
  }
.TodoItem{
    border: 1px solid #0BDE51;
    list-style: none;
    width: 90%;
    margin: 45px auto; 
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    font-size: 20px;
    background-color: #A4FA0D;
    box-shadow: 0px 3px 15px #0BDE51; 
}

.Icon-check{
    color: white;
    font-size: 60px;
    position: relative;
    bottom: 10px;
    margin-left: 0;
    cursor: pointer;
}

.Icon-check--active{
    color: #0DFA9F;
    font-size: 70px;
}

.Icon-check:hover{
    color: #0DFA9F;
    font-size: 60px;
}

.Icon-delete{
    position: relative;
    bottom: 35px;
    font-size: 40px;
    color: red;
    cursor: pointer;
}

.Icon-delete:hover{
    font-weight: bold;
    font-size: 42px;
}

.TodoItem-p--complete {
    text-decoration: line-through;
  }

.modalStyles{
    background: #60fd68;
    position: fixed;
    top: -10px;
    bottom: -10px;
    left: -10px;
    right: -10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: black;
}
